<template>
    <div
        id="textInput"
        ref="textInput"
        :contenteditable="!props.disabled"
        class="form-control p-2"
        :class="{'temp text-muted': placeholderActive}"
        :innerText="displayText"
        @input="onInput"
        @focus="hidePlaceholder=true"
        @focusout="hidePlaceholder=false"
        @keydown.enter="onKeyDownEnter"
        >
    </div>
</template>
<script setup>
import { ref, watch, defineModel, defineEmits, defineProps, computed, useTemplateRef } from 'vue'
const model = defineModel();
const localText = ref(model.value);
const hidePlaceholder = ref(false);
const textInput = useTemplateRef("textInput");
const emit = defineEmits(['input','keydown.enter']);

const props = defineProps({
    disabled:{type:Boolean},
    placeholder:{type:String},
    editorVisible:{type:Boolean},
})

const placeholderActive = computed(()=>{
    return displayText.value === props.placeholder && !hidePlaceholder.value;
})

const displayText = computed(()=>{
    if (model.value.trim() ==="" && !hidePlaceholder.value){
        return props.placeholder;
    } else {
        return localText.value;
    }
})

const onKeyDownEnter = (event) => {
    event.preventDefault();
    // I know this is deprecated but I had to do it.
    // The normal 'Enter' behavior was causing weird
    // double linebreaks.
    document.execCommand("insertText",false,"\n");
    emit('keydown.enter');
}

const onInput = (event) => {
    model.value = event.target.innerText;
    emit('input');
}

// Focus the div when the editor becomes visible
watch(
    () => props.editorVisible,
    (newValue) => {
        if(newValue && textInput){
            setTimeout(() => {
                const range = document.createRange();
                const selection = window.getSelection();
                range.selectNodeContents(textInput.value);
                range.collapse(false); // <-- Set the cursor at the end of the selection
                selection?.removeAllRanges();
                selection?.addRange(range);
                textInput.value.focus();
            }, 0);
        }
    },{immediate:true}
)

</script>
<style scoped>
div {
    min-height: 2.5em;
    white-space: pre-wrap !important;
    overflow-wrap: normal !important;
}
.temp {
    user-select: none;
}
</style>